.hero-text {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  color:black;
  font-size: 17px;
  }

  .changeTheColor {
    -webkit-animation-name: example; 
    -webkit-animation-duration: 7s; 
    animation-name: example;
    animation-duration: 7s; 
   
  }
         
        
        @keyframes example {
          0%   { opacity: 0; }
          25%  {color: rgb(247, 51, 17);}
          50%  {color: rgb(218, 233, 12);}
          70%  {color: rgb(8, 153, 39);}
          85%  {color: rgb(35, 11, 248);}
          100% { opacity: 1;}
        }
   
        /* Safari 4.0 - 8.0 */
        @-webkit-keyframes example{
          0%   { opacity: 0; }
          25%  {color: rgb(247, 51, 17);}
          50%  {color: rgb(218, 233, 12);}
          70%  {color: rgb(8, 153, 39);}
          85%  {color: rgb(35, 11, 248);}
          100% { opacity: 1; }
        }
       
        .line
          {
            height:0px;
            width:1px;
            border-bottom:2px solid #000;
            -webkit-animation: increase 10s;
            -moz-animation:    increase 10s; 
            -o-animation:      increase 10s; 
            animation:         increase 10s; 
            animation-fill-mode: forwards;
          }
          
          @keyframes increase {
            100% {
                width: 800px;
            }
        }
          

        /* Safari 4.0 - 8.0 */
        @-webkit-keyframes increase {
          100% {
              width: 800px;
          }
      }

      .MyName {
        position: relative;
        -webkit-animation-name: noorName; 
        -webkit-animation-duration: 5s; 

        animation-name: noorName;
        animation-duration: 5s;
      }

      @keyframes noorName {
        from {left: -300px;}
        to{left: 0;}
       
      }
        
      /* Safari 4.0 - 8.0 */
      @-webkit-keyframes noorName{
        from {left: -300px;}
        to{left: 0;}
      }


